<template>
  <div>
    <div
      class="text-white text-center font-bold p-5 mb-4"
      v-if="reg_show_alert"
      :class="reg_alert_varient"
    >
      {{ reg_alert_msg }}
    </div>
    <vee-form :validation-schema="schema" @submit="register" :initial-values="userData">
      <!-- Name -->
      <div class="mb-3">
        <label for="name" class="inline-block mb-2 w-full"> Name</label>
        <vee-field
          name="name"
          type="text"
          class="block w-full py-1.5 px-3 text-gray-800 border border-gray-300 transition duration-500 focus:outline-none focus:border-black rounded"
          placeholder="Enter Name"
        />
        <error-message class="text-red-600" name="name" />
      </div>
      <!-- Email -->
      <div class="mb-3">
        <label for="email" class="inline-block mb-2 w-full"> Email</label>
        <vee-field
          name="email"
          type="email"
          class="block w-full py-1.5 px-3 text-gray-800 border border-gray-300 transition duration-500 focus:outline-none focus:border-black rounded"
          placeholder="Enter Email"
        />
        <error-message class="text-red-600" name="email" />
      </div>
      <!-- Age -->
      <div class="mb-3">
        <label for="age" class="inline-block mb-2 w-full"> Age</label>
        <vee-field
          name="age"
          type="number"
          class="block w-full py-1.5 px-3 text-gray-800 border border-gray-300 transition duration-500 focus:outline-none focus:border-black rounded"
        />
        <error-message class="text-red-600" name="age" />
      </div>
      <!-- Password -->
      <div class="mb-3">
        <label for="password" class="inline-block mb-2 w-full">Password</label>
        <vee-field name="password" :bails="false" v-slot="{ field, errors }">
          <input
            type="password"
            class="block w-full py-1.5 px-3 text-gray-800 border border-gray-300 transition duration-500 focus:outline-none focus:border-black rounded"
            placeholder="Password"
            v-bind="field"
          />
          <div class="text-red-600" v-for="error in errors" :key="error">{{ error }}</div>
        </vee-field>
        <!-- <error-message class="text-red-600" name="password" /> -->
      </div>
      <!-- Confirm Password -->
      <div class="mb-3">
        <label for="confirm_password" class="inline-block mb-2 w-full">Confirm Password</label>
        <vee-field
          name="confirm_password"
          type="password"
          class="block w-full py-1.5 px-3 text-gray-800 border border-gray-300 transition duration-500 focus:outline-none focus:border-black rounded"
          placeholder="Confirm Password"
        />
        <error-message class="text-red-600" name="confirm_password" />
      </div>
      <!-- Country -->
      <div class="mb-3">
        <label for="country" class="inline-block mb-2 w-full">Country</label>
        <vee-field
          as="select"
          name="country"
          class="block w-full py-1.5 px-3 text-gray-800 border border-gray-300 transition duration-500 focus:outline-none focus:border-black rounded"
        >
          <option value="USA">USA</option>
          <option value="Mexico">Mexico</option>
          <option value="Germany">Germany</option>
          <option value="lanka">Sri Lanka</option>
        </vee-field>
        <error-message class="text-red-600" name="country" />
      </div>
      <!-- TOS -->
      <div class="mb-3 pl-6">
        <label for="checkbox" class="inline-block w-full">Accept terms of service</label>
        <vee-field
          value="1"
          name="tos"
          type="checkbox"
          class="w-4 h-4 float-left -ml-6 mt-1 rounded"
        />
        <error-message class="text-red-600 block" name="tos" />
      </div>
      <button
        type="submit"
        :disabled="reg_in_submission"
        class="block w-full bg-purple-600 text-white py-1.5 px-3 rounded transition hover:bg-purple-700"
      >
        Submit
      </button>
    </vee-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      schema: {
        name: 'required|min:3|max:100',
        email: 'required|min:8|max:100|email',
        age: 'required|min_value:18|max_value:100',
        password: 'required|min:5|max:100',
        confirm_password: 'passwords_mismatch:@password',
        country: 'required|country_excluded:lanka',
        tos: 'required',
      },
      userData: {
        country: 'USA',
      },
      reg_in_submission: false,
      reg_show_alert: false,
      reg_alert_varient: 'bg-blue-500',
      reg_alert_msg: 'Please wait! Your account is being created.',
    };
  },
  methods: {
    async register(values) {
      this.reg_in_submission = true;
      this.reg_show_alert = true;
      this.reg_alert_varient = 'bg-blue-500';
      this.reg_alert_msg = 'Please wait! Your accunt is being created.';

      try {
        await this.$store.dispatch('auth/resister', values);
      } catch (error) {
        console.log(error);
        this.reg_in_submission = false;
        this.reg_alert_varient = 'bg-red-500';
        this.reg_alert_msg = 'An unexpected error occured. Please. try again later. ';
        return;
      }

      this.reg_in_submission = false;
      this.reg_alert_varient = 'bg-green-500';
      this.reg_alert_msg = 'Success! Your accunt has been created.';
      window.location.reload();
    },
  },
};
</script>
