<template>
  <!-- Header -->
  <header id="header" class="bg-gray-700">
    <nav class="container mx-auto flex justify-start items-center py-5 px-4">
      <!-- App Name -->
      <router-link
        exact-active-class="no-active"
        class="text-white font-bold uppercase text-2xl mr-4"
        to="/"
        >Music</router-link
      >
      <div class="flex flex-grow items-center">
        <!-- Primary Navigation -->
        <ul class="flex flex-row mt-1">
          <!-- Navigation Links -->
          <li v-if="!userLoggedIn">
            <a class="px-2 text-white" href="#" @click.prevent="toggleAuthModal"
              >Login / Register</a
            >
          </li>
          <template v-else>
            <li>
              <router-link class="px-2 text-white" :to="{ name: 'manage-router' }"
                >Manage</router-link
              >
            </li>
            <li>
              <a class="px-2 text-white" @click.prevent="logout" href="#">Logout</a>
            </li>
          </template>
          <li>
            <router-link class="px-2 text-white" :to="{ name: 'about-router' }">About</router-link>
          </li>
        </ul>
      </div>
    </nav>
  </header>
</template>

<script>
export default {
  name: 'Header',
  methods: {
    toggleAuthModal() {
      this.$store.commit('auth/toggleAuthModal');
    },
    logout() {
      this.$store.dispatch('auth/signout');
      this.$router.replace({ name: 'home' });
    },
  },
  computed: {
    userLoggedIn() {
      return this.$store.getters['auth/userLoggedIn'];
    },
  },
};
</script>
