import { auth, userCollections } from '@/includes/firebase';

export default {
  namespaced: true,
  state: {
    authModalShow: false,
    userLoggedIn: false,
  },
  getters: {
    authModalShow(state) {
      return state.authModalShow;
    },
    userLoggedIn(state) {
      return state.userLoggedIn;
    },
  },
  mutations: {
    toggleAuthModal(state) {
      state.authModalShow = !state.authModalShow;
      console.log(state.authModalShow);
    },
    toggleAuth(state) {
      state.userLoggedIn = !state.userLoggedIn;
    },
  },
  actions: {
    async resister(context, payload) {
      const userCred = await auth.createUserWithEmailAndPassword(payload.email, payload.password);

      await userCollections.doc(userCred.user.uid).set({
        name: payload.name,
        email: payload.email,
        age: payload.age,
        country: payload.country,
      });

      userCred.user.updateProfile({
        displayName: payload.name,
      });

      context.commit('toggleAuth');
    },
    async login(context, payload) {
      await auth.signInWithEmailAndPassword(payload.login_email, payload.login_password);

      context.commit('toggleAuth');
    },
    init_login(context) {
      const user = auth.currentUser;
      if (user) {
        context.commit('toggleAuth');
      }
    },
    async signout(context) {
      await auth.signOut();
      context.commit('toggleAuth');
    },
  },
};
