<template>
  <div>
    <div
      class="text-white text-center font-bold p-5 mb-4"
      v-if="login_show_alert"
      :class="login_alert_varient"
    >
      {{ login_alert_msg }}
    </div>
    <vee-form :validation-schema="schema_login" @submit="login">
      <!-- Email -->
      <div class="mb-3">
        <label for="login_email" class="inline-block mb-2 w-full">Email</label>
        <vee-field
          name="login_email"
          type="email"
          class="block w-full py-1.5 px-3 text-gray-800 border border-gray-300 transition duration-500 focus:outline-none focus:border-black rounded"
          placeholder="Enter Email"
        />
        <error-message class="text-red-600" name="login_email" />
      </div>
      <!-- Password -->
      <div class="mb-3">
        <label for="login_password" class="inline-block mb-2 w-full"> Password</label>
        <vee-field
          name="login_password"
          type="password"
          class="block w-full py-1.5 px-3 text-gray-800 border border-gray-300 transition duration-500 focus:outline-none focus:border-black rounded"
          placeholder="Password"
        />
        <error-message class="text-red-600" name="login_password" />
      </div>
      <button
        type="submit"
        :disabled="login_in_submission"
        class="block w-full bg-purple-600 text-white py-1.5 px-3 rounded transition hover:bg-purple-700"
      >
        Submit
      </button>
    </vee-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      schema_login: {
        login_email: 'required|min:4|max:100|email',
        login_password: 'required|min:5|max:100',
      },
      login_in_submission: false,
      login_show_alert: false,
      login_alert_varient: 'bg-blue-500',
      login_alert_msg: 'Please wait! We are loggi you in.',
    };
  },
  methods: {
    async login(values) {
      this.login_in_submission = true;
      this.login_show_alert = true;
      this.login_alert_varient = 'bg-blue-500';
      this.login_alert_msg = 'Please wait! We are loggi you in.';

      try {
        await this.$store.dispatch('auth/login', values);
      } catch (error) {
        console.log(error);
        this.login_in_submission = false;
        this.login_alert_varient = 'bg-red-500';
        this.login_alert_msg = 'An unexpected error occured. Please. try again later. ';
        return;
      }

      this.login_alert_varient = 'bg-green-500';
      this.login_alert_msg = 'Success! Your are now login.';
      window.location.reload();
    },
  },
};
</script>
